.gift {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  position: relative;
  width: 100%;
  padding: 30px 60px;
  margin: 30px auto 0;
  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    transform: translateX(-50%);
    z-index: -1;
  }
  &--yellow::before {
    background-image: url(../../img/bg/background_cta_yellow.svg);
  }
  @media (min-width: $min-tablet) {
    text-align: center;
    padding: 30px 120px;
    &::before {
      width: 100%;
      height: 225px;
    }
  }
  @media (min-width: $min-desktop) {
    max-width: 1100px;
    &::before {
      height: 165px;
      max-width: 1082px;
    }
  }
}

.gift p {
  width: 100%;
  text-align: center;
}

.gift ul {
  display: grid;
  font-weight: bold;
  grid-template-columns: 1fr;
  margin-bottom: 0;
  padding-left: 20px;
  list-style: none;
  width: 100%;
  text-align: center;
  padding: 0;
  @media (min-width:$min-desktop) {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    list-style: disc;
    text-align: left;
    padding-left: 20px;
  }
}

.gift .button {
  padding: 15px 40px;
  width: fit-content;
  margin: 0 auto;
  font-weight: bold;
  @media (min-width: $min-desktop) {
    order: -1;
  }
}

.gift__title {
  margin-bottom: 0;
  width: 100%;
  @include font_subtitles;
  text-indent: 0;
  @media (min-width: $min-tablet) {
    font-size: 28px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  }
  @media (min-width: $min-desktop) {
    order: -2;
    width: fit-content;
  }
}
